
.dataInput {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
}
.flexContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.input {
  text-align: center;
  background-color: #141933;
  color: #dddddd;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border: none;
  height: 1.5rem;
  margin-top: 1rem;
}
.input:focus {
  outline: none;
}
.select {
  background-color: #141933;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border: none;
  height: 1.5rem;
  margin-top: 0.5rem;
}
.label {
  margin-top: 0.75rem;
  color: white;
}
.costDisplay {
  width: 80%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: 1rem;
  margin: 1rem auto 2rem auto;
}
.text {
  text-align: center;
  margin-top: 1rem;
}
.btn {
  width: 50%;
  margin: 0 auto 1rem auto;
  height: 1.25rem;
  border: none;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  background: linear-gradient(
    90deg,
    rgba(255, 159, 96, 1) 0%,
    rgba(252, 91, 96, 1) 50%,
    rgba(152, 59, 125, 1) 100%
  );
}
.white {
  color: white;
  width: 100%;
}
@media only screen and (max-width: 850px) {
.text {
  padding-top: 1rem;
}
.costDisplay {
  padding: 2rem 0;;
}
}
